:global .login {
  background: url(./images/bg.png) no-repeat center top;
  background-size: cover; }
  :global .login input::-webkit-input-placeholder,
  :global .login textarea::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #fff; }
  :global .login input:-moz-placeholder,
  :global .login textarea:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #fff; }
  :global .login input::-moz-placeholder,
  :global .login textarea::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #fff; }
  :global .login input:-ms-input-placeholder,
  :global .login textarea:-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #fff; }

